.userBoard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
}

.userInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    border-bottom: 0.5px solid #b2b2b2;
}

.userIcon {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #b2b2b2;
    margin-top: 20px;
}

.user {
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

.userBtn {
    margin-left: 20px;
    font-size: 14px;
}

.text {
    margin-left: 5px;
}

.userName {
    margin: auto;
    font-size: 24px;
    font-weight: 500;
}

.postQuantity {
    display: flex;
    width: 400px;
    justify-content: space-between;
}

.menu {
    display: flex;
    width: 150px;
    justify-content: space-between;
    margin-top: 20px;
}

